.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1200;
  
}

.content {
  font-size: 20px;
  color: brown;
  position: relative;
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

.content img {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
  display: block;
  object-fit: cover;
}
